import {Component, OnInit, ViewChild, ElementRef, ContentChild, asNativeElements} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {Http, RequestOptions} from '@angular/http';
import {Router} from '@angular/router';

@Component({
  selector: 'app-terminal',
  templateUrl: './terminal.component.html',
  styleUrls: ['./terminal.component.css']
})

export class TerminalComponent implements OnInit {
  content: any;
  temp_directory: any;
  current_folders: any;

  high :any
  @ViewChild ('conversation') convo: ElementRef;
  @ViewChild ('input') input: ElementRef;
  public myform: FormGroup;
  commands = [];
  data = [];
  user_input = [];
  temp: any;
  nav : any;
  counter: any;
  current_directory: any;
  commandsize : any;
  @ViewChild('terminal') terminal: ElementRef
;
  i = 0;
  private target: Element | any | EventTarget;
  private headers: any;
  private options: RequestOptions;
  constructor(private http: Http, private eleme: ElementRef, private router: Router) {
    this.counter = 0;
    this.user_input.push('hi');
    this.data.push('hi');
    this.current_directory = 'home';
    this.myform = new FormGroup({
      'data': new FormControl('', [Validators.required])
    });
  }

  ngOnInit() {
    this.getDirectory();
this.high = this.terminal.nativeElement.scrollTop
  }
  // check level starts
  checkLs() {
    this.headers = new Headers({ 'Content-Type': 'application/json' });
    this.options = new RequestOptions({headers: this.headers });
    return this.http.post( 'https://whispering-reef-16492.herokuapp.com/ls', { 'data' : this.current_directory }, this.options);
  }
  // check level ends
  // Set Directory starts
  setDirectory(temp) {
    let flag = 0;
    temp = temp.replace('cd', '');
    temp =  temp.replace(' ','');
    console.log(this.current_folders);
    for (this.i = 0; this.i < this.current_folders.length; this.i++ ) {
      if (this.current_folders[this.i] === temp) {
        flag = 1;
        this.current_directory = 'home/' +  temp;
        console.log ('setdirectory');
        this.getDirectory();
      }
    }
    if (flag) {
      this.convo.nativeElement.insertAdjacentHTML('beforeEnd', `<p class="line1" style="font-size:130%;color:white;font-family:monospace;margin:0"> > ` + this.temp + `</p>` );
      console.log ('valid directory');
      this.input.nativeElement.value = '';
    } else {
      this.convo.nativeElement.insertAdjacentHTML('beforeEnd' , `<p class="line1" style="font-size:130%;color:white;font-family:monospace;margin:0"> > ` + this.temp + `</p>` + `<p> Invalid path type help to get the list of commands
 </p>`);
      this.input.nativeElement.value = '';

    }
  }
  // Set Directory ends
  // Get Directory starts
    getDirectory() {
      this.headers = new Headers({ 'Content-Type': 'application/json' });
      this.options = new RequestOptions({headers: this.headers });
      return this.http.post( 'https://whispering-reef-16492.herokuapp.com/ls', { 'data' :  this.current_directory }, this.options).subscribe(data => {
        this.current_folders = JSON.parse(data['_body']);
      });
    }

  // Get Directory ends
  // printFile starts
  printFile(temp) {
    temp = temp.replace('cat', '');
    temp =  temp.replace(' ', '');
    this.headers = new Headers({ 'Content-Type': 'application/json' });
    this.options = new RequestOptions({headers: this.headers });
    return this.http.post( 'https://whispering-reef-16492.herokuapp.com/read', { 'data' :  this.current_directory, 'file' : temp }, this.options).subscribe(data => {
      this.content = JSON.parse(data['_body']);
      this.convo.nativeElement.insertAdjacentHTML('beforeEnd', `<p class="line1" style="font-size:130%;color:white;font-family:monospace;margin:0"> > ` + this.temp + `</p>` + `<p class="line1" style="font-size:130%;color:white;font-family:monospace;margin:0"> > ` + this.content + `</p>`);
    });
  }
  // printFile ends
  printHelp() {
    this.headers = new Headers({ 'Content-Type': 'application/json' });
    this.options = new RequestOptions({headers: this.headers });
    return this.http.post( 'https://whispering-reef-16492.herokuapp.com/help', { 'data' :  this.current_directory, 'file' : 'help.txt' }, this.options).subscribe(data => {
      this.content = JSON.parse(data['_body']);
      this.convo.nativeElement.insertAdjacentHTML('beforeEnd', `<p class="line1" style="font-size:130%;color:white;font-family:monospace;margin:0"> > ` + this.temp + `</p>` + `<p class="line1" style="font-size:130%;color:white;font-family:monospace;margin:0"> > ` + this.content + `</p>`);
  });
  }
  eventHandler(event) {
    console.log('key', event.keyCode);
    this.temp = this.input.nativeElement.value;
    console.log(this.temp);

    if (event.keyCode === 38) {
      console.log("Up");
      console.log(this.commands);
      console.log(this.nav);
      if (this.nav) {

        this.input.nativeElement.value = this.commands[this.nav - 1];
        this.nav--;
      }
    }
    if (event.keyCode === 40) {
      console.log(this.nav);
      if(this.nav < this.commands.length - 1) {
        console.log(this.nav);
        this.nav++;
        this.input.nativeElement.value = this.commands[this.nav - 1];
      }
    }
    if (event.keyCode === 9) {
      event.preventDefault();
      this.check_cd(this.temp);
    }
    if (event.keyCode === 13) {

      this.temp = this.input.nativeElement.value;
      this.commands.push(this.temp);
      this.nav = this.commands.length;
      if (this.temp === 'help') {
        this.printHelp();
      }
      if (this.temp === 'ls') {
       this.checkLs().subscribe( data => {
         this.current_folders = JSON.parse(data['_body']);
         this.convo.nativeElement.insertAdjacentHTML('beforeEnd', `<p class="line1" style="font-size:130%;color:white;font-family:monospace;margin:0"> >` + ' ' + this.temp + `</p>` + ` <p class="line1" style="font-size:130%;color:white;font-family:monospace;margin:0"> > ` + this.current_folders + `</p>`)
        });
       this.input.nativeElement.value = '';
        console.log(this.current_folders);
      } else if (this.temp.includes('cd')) {
          if (this.temp.includes('.')) {
            this.current_directory = 'home';
            this.convo.nativeElement.insertAdjacentHTML('beforeEnd', `<p class="line1" style="font-size:130%;color:white;font-family:monospace;margin:0"> >  cd .. </p>`); this.input.nativeElement.value = '';

            this.getDirectory();
          } else {
            console.log ('else part');
            this.setDirectory(this.temp);
          }
        } else if (this.temp.includes('cat')) {
        this.printFile(this.temp);
        this.input.nativeElement.value = '';
      } else {
        this.input.nativeElement.value = '';

      }
      this.scrollToBottom()
    }
  }
  scrollToBottom(): void {
    try {
      console.log(this.terminal.nativeElement.scrollHeight)
      this.terminal.nativeElement.scrollTop =  this.terminal.nativeElement.scrollHeight - this.high ;

      console.log(this.terminal.nativeElement.scrollTop)
    } catch(err) {
console.log(err)
    }
  }
  check_cd (str) {
    console.log('str',str);
    this.counter = 0;
    if ( str) {
      if (str.includes('cd')) {
        this.counter = 1;
      }
      if (str.includes('cat'))
      {
        this.counter=2;
      }
      str = str.replace('cd', '');
      str = str.replace('cat', '');
      str = str.replace(' ', '');
      if (str.length > 0) {
        console.log('str1111', str);
        for (this.i = 0; this.i < this.current_folders.length; this.i++ ) {
          if (this.current_folders[this.i].startsWith(str) === true) {
            str = this.current_folders[this.i];
            this.temp_directory = str;
            console.log('str', str);
          }
        }
      }
    }
    if (this.counter === 1) {
      this.input.nativeElement.value = 'cd '  + str;
    } else if (this.counter === 2) {
      this.input.nativeElement.value = 'cat ' + str;
    } else if (this.counter === 3) {
      this.input.nativeElement.value = '' + str;
    }
  }

}



