import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  width = 0;
  w = '' ;
  i = 0;
  perc = 0;
  success = false;
  hide = true;

  ngOnInit() {
    this.getWidth();
  }
  getWidth() {
    for ( this.i = 0; this.i < 60; this.i++) {
      setTimeout(() => {
        this.width = this.width + 5;
        console.log(this.width);
        this.perc = Math.ceil((this.width / 300 ) * 100) ;
        this.w = this.width + 'px';
        if (this.width === 300) {
          this.success = true;
        }
      }, 150 * this.i);
    }
  }
  clear () {
    this.hide = false;
  }
}



